import { Fragment } from 'react'
import PropTypes from 'prop-types'
import NavDrawer, {
  DrawerHeader,
  DrawerSubtitle,
  DrawerTitle,
  DrawerContent,
} from '@material/react-drawer'
import DrawerList from './DrawerList'

const Contents = ({ isMobile, setIsDrawerOpen }) => (
  <Fragment>
    <DrawerHeader>
      <DrawerTitle>
        Drawer Title
      </DrawerTitle>
      <DrawerSubtitle>
        Drawer Subtitle
      </DrawerSubtitle>
    </DrawerHeader>
    <DrawerContent>
      <DrawerList
        onItemClick={() => setIsDrawerOpen(false)}
      />
    </DrawerContent>
  </Fragment>
)
Contents.propTypes = {
  isMobile: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

const Drawer = ({ isMobile, isOpen, setIsDrawerOpen }) => (
  <>
    <div
    >
    <NavDrawer
      className='app-drawer-mobile'
      modal={true}
      open={isOpen}
      onOpen={() => setIsDrawerOpen(true)}
      onClose={() => setIsDrawerOpen(false)}
    >
      <Contents
        isMobile={isMobile}
        setIsDrawerOpen={setIsDrawerOpen}
      />
    </NavDrawer>
    </div>
    <div
    >
    <NavDrawer
      className='app-drawer-desktop'
    >
      <Contents
        isMobile={isMobile}
        setIsDrawerOpen={setIsDrawerOpen}
      />
    </NavDrawer>
    </div>
    <style jsx>{`
      @media (min-width: 840px) {
        .app-drawer-mobile {
          display: none !important;
        }
      }
      @media (max-width: 839px) {
        .app-drawer-desktop {
          display: none !important;
        }
      }
    `}</style>
  </>
)
Drawer.propTypes = {
  isMobile: PropTypes.bool.isRequired,
  isOpen: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

export default Drawer
